.signup-container {
  display: flex;
  width: 100%;
  height: 100%;

  .left,
  .right {
    justify-content: center;
    align-items: center;
    width: 50%;
  }

  .left-content {
    display: flex;
    flex-direction: column;
    width: 60%;

    .info {
      align-self: center;
      margin-left: auto;
      font-size: 0.8rem;
      color: #595555;
    }
    .helper-txt {
      color: #A09B9B;
    }
  }

  .right {
    background-color: #5d5fef;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 2rem;
    height: 100vh;
    display: flex;
    flex-direction: column;

    .full {
      flex: 1;
    }

    .signin {
      padding: 2rem;
      justify-content: end;
      color: white;
      cursor: pointer;
      width: 100%;
    }

    .offer {
      margin: 3%;
      line-height: 2rem;
      background-color: #ffd583;
      padding: 2%;
      border-radius: 5rem;
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 87%;

      .icon {
        background-color: white;
        padding: 0.5rem;
        border-radius: 50%;
      }

      ul {
        margin-left: 2.5rem;
      }
    }

    .right-content {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      width: 100%;

      .steps {
        // flex: 1;
        .signup {
          max-width: 100%;
          max-height: 100%;
          height: 60vh;
          position: relative;
        }

        .logo {
          position: absolute;
          right: 2rem;
          bottom: 2rem;
        }
      }
    }
  }

}
