.header {
    position: fixed;
    padding: 2rem 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    
  .logo-container {
    display: flex;
    align-items: center;

    img:first-child {
      border-right: 1px solid #ebebeb;
      margin-right: 1rem;
      padding-right: 0.8rem;
    }
  }
}
